<template>
  <div class="personal clearfix">
    <div class="index-content">
      <div class="personal-box">
        <h2 class="personal-title">个人资料</h2>
        <el-row class="personal-info">
          <el-col :span="4" style="line-height: 72px;">
            <span class="personal-info-title">头像</span>
          </el-col>
          <el-col :span="20">
            <img class="personal-info-image" :src="infoImage" alt>
            <div class="personal-info-imgopt">
              <div class="personal-info-tip">支持 jpg、png 格式大小 5M 以内的图片</div>
              <el-upload
                action="http://localhost:3000/imgUp"
                :limit="1"
                :show-file-list="false"
                :on-success="handleSuccess"
                :on-error="handleError"
                :file-list="fileList"
              >
                <el-button class="personal-info-imgbtn" type="primary" size="mini">点击上传</el-button>
              </el-upload>
            </div>
          </el-col>
          <el-col :span="4" style="line-height:40px;">
            <span class="personal-info-title">用户名</span>
          </el-col>
          <el-col :span="20">
            <span class="personal-info-text" v-show="!isModName">{{name}}</span>
            <el-input class="personal-info-input" v-model="username" v-show="isModName"></el-input>
            <div class="personal-info-opt">
              <el-button type="text" v-show="!isModName" @click="modName">修改</el-button>
              <el-button type="text" v-show="isModName" @click="sureModeName">保存</el-button>
              <el-button type="text" v-show="isModName" @click="cancleName">取消</el-button>
            </div>
          </el-col>
          <el-col :span="4" style="line-height:40px;">
            <span class="personal-info-title">github关联</span>
          </el-col>
          <el-col :span="20">
            <span class="personal-info-text"></span>
            <div class="personal-info-opt">
              <el-button type="text" v-show="!isBindGithub">绑定</el-button>
              <el-button type="text" v-show="isBindGithub">解除绑定</el-button>
            </div>
          </el-col>
          <el-col :span="4" style="line-height:40px;">
            <span class="personal-info-title">密码</span>
          </el-col>
          <el-col :span="20">
            <el-button type="primary" size="mini" @click="modPwdInfo">修改密码</el-button>
          </el-col>
        </el-row>
      </div>
    </div>

    <el-dialog title="修改密码" :visible.sync="pwdVisible" width="500px">
      <el-row class="personal-pwdInfo">
        <el-col :span="24">
          <el-input v-model="password" placeholder="请输入密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24">
          <el-input v-model="newPassword" placeholder="请输入新密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24">
          <el-input v-model="reNewPassword" placeholder="请再输入新密码" size="medium" type="password"></el-input>
        </el-col>
        <el-col :span="24">
          <el-button type="primary" size="medium" class="personal-pwdInfo-btn" @click="modSure">提交</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import { modPassword, modName, modImg } from "@/api/user";
import { getCookie } from "@/utils/cookies";

export default {
  name: "personal",
  data() {
    return {
      infoImage: "",
      name: "",
      username: "",
      pwdVisible: false,
      password: "",
      newPassword: "",
      reNewPassword: "",
      userId: "",
      fileList: "",
      isModName: false,
      isBindGithub: false,
      fileList: []
    };
  },
  methods: {
    modName() {
      this.isModName = true;
      this.username = this.name;
    },
    cancleName() {
      this.isModName = false;
      this.username = this.name;
    },
    sureModeName() {
      if (!this.username) {
        this.$message.error("请输入用户名");

        return false;
      }

      modName({
        id: this.userId,
        account: this.username
      }).then(res => {
        const data = res.data;
        
        if (data.ret == 0) {
          this.$message.error(data.msg);
        } else {
          this.$message.success("修改用户名成功！");
          this.name = this.username;
          this.isModName = false;
        }
      });
    },
    handleSuccess(res) {
      const data = {
        imgId: res.id,
        id: this.userId
      }

      modImg(data).then(res => {
         const data = res.data;
        
        if (data.ret == 0) {
          this.$message.error(data.msg);
        } else {
          this.$message.success("修改图片成功！");
          this.infoImage = "http://localhost:3000/imgUp/file?id=" + data.imgId;
        }
      })
    },
    handleError() {},
    modPwdInfo() {
      this.pwdVisible = true;
    },
    modSure() {
      if (!this.password) {
        this.$message.error("请输入密码");

        return false;
      }

      if (!this.newPassword) {
        this.$message.error("请输入新密码");

        return false;
      }

      if (!this.reNewPassword) {
        this.$message.error("请再输入新密码");

        return false;
      }

      if (this.newPassword != this.reNewPassword) {
        this.$message.error("密码不一致");

        return false;
      }

      modPassword({
        id: this.userId,
        oldPassword: this.password,
        newPassword: this.newPassword
      }).then(res => {
        const data = res.data;

        if (data.ret == 0) {
          this.$message.error(data.msg);
        } else {
          this.$message.success("修改密码成功！");
          this.pwdVisible = false;
        }
      });
    },
    getInitData() {
      const userInfo = JSON.parse(decodeURIComponent(getCookie("userInfo")));

      this.infoImage = userInfo.imgId
        ? "http://localhost:3000/imgUp/file?id=" + userInfo.imgId
        : require("@/assets/logo.png");
      this.name = userInfo.account;
      this.userId = userInfo._id;

      console.log(getCookie("userInfo"));
    }
  },
  mounted() {
    this.getInitData();
  }
};
</script>

<style>
.personal .personal-box {
  padding: 14px 40px 50px 20px;
}

.personal-info {
  margin-top: 20px;
}

.personal-info > .el-col {
  padding: 10px 0 20px;
}

.personal-info-image,
.personal-info-imgopt {
  display: inline-block;
  vertical-align: top;
}

.personal-info-image {
  width: 72px;
  height: 72px;
  margin-right: 16px;
}

.personal-info-tip {
  margin-bottom: 10px;
  font-size: 13px;
  color: #828282;
}

.personal-info-text,
.personal-info-input,
.personal-info-opt {
  display: inline-block;
  vertical-align: middle;
}

.personal-info-text,
.personal-info-input {
  width: 80%;
}

.personal-info-opt {
  width: 20%;
  text-align: right;
}

.personal .el-input__inner {
  border-color: #ffffff;
}

.personal-title {
  font-size: 24px;
  font-weight: bold;
}

.personal-pwdInfo > .el-col {
  margin-bottom: 20px;
}

.personal-pwdInfo-btn {
  width: 100%;
}
</style>

